import * as React from "react";
import { graphql, useStaticQuery } from "gatsby";

type SeoQueryData = {
  site: {
    siteMetadata: {
      title: string;
      siteUrl: string;
    };
  };
};
type SeoProps = {
  title?: string;
  og?: {
    path: string;
    type: string;
    title: string;
    description: string;
    image?: string;
  };
};
const Seo = ({ title, og }: SeoProps) => {
  const data = useStaticQuery<SeoQueryData>(graphql`
    query {
      site {
        siteMetadata {
          title
          siteUrl
        }
      }
    }
  `);

  const { title: siteTitle, siteUrl } = data.site.siteMetadata;

  return (
    <>
      <title>{title ? `${title} | ${siteTitle}` : siteTitle}</title>
      {og && (
        <>
          <meta property="og:url" content={`${siteUrl}/${og.path}`} />
          <meta property="og:type" content={`${og.type}`} />
          <meta property="og:title" content={`${og.title}`} />
          <meta property="og:description" content={`${og.description}`} />
          {!!og.image && (
            <meta property="og:image" content={`${siteUrl}${og.image}`} />
          )}

          <meta property="twitter:card" content="summary_large_image" />
          <meta property="twitter:url" content={`${siteUrl}/${og.path}`} />
          <meta property="twitter:title" content={`${og.title}`} />
          <meta property="twitter:description" content={`${og.description}`} />
          {!!og.image && (
            <meta property="twitter:image" content={`${siteUrl}${og.image}`} />
          )}
        </>
      )}
    </>
  );
};

export default Seo;
